.w-table {
  display: table;
  border-collapse: collapse;
  position: relative;
  width: 100%;
  border-spacing: 0;
  font-size: 12px;
}

.w-table__interactive {

  .w-table_tr:not(.w-table_tr__selected) {
    cursor: pointer;

    &:hover {

      .w-table_td {
        background-color: #f2f2f2;
        border-color: #f2f2f2;
      }
    }
  }

  tr.w-table_tr[disabled] {
    cursor: default !important;
  }
}

.w-table__vertical {

  .w-table_th {
    border-bottom: 1px solid #f2f2f2;
    border-top: 1px solid #f2f2f2;
    padding: size_static("s", "desktop");
  }
}

.w-table_td {
  display: table-cell;
  border-bottom: 1px solid #f2f2f2;
  border-top: 1px solid #f2f2f2;
  padding: size_static("s", "desktop");

  &:first-child {
    padding-left: 0;
  }

  &:last-child {
    padding-right: 0;
  }
}

.w-table_th {
  display: table-cell;
  border-top: 1px solid #cdcdcd;
  border-bottom: 1px solid #cdcdcd;
  padding: 0 size_static("s", "desktop") size_static("s", "desktop");
  text-align: left;
  font-weight: 700;

  &:first-child {
    padding-left: 0;
  }

  &:last-child {
    padding-right: 0;
  }
}

.w-table_tr {
  display: table-row;

  &.w-table_tr__selected {

    .w-table_td {
      background-color: #cdcdcd;
      border-color: #cdcdcd;
    }
  }

  &:first-child {

    .w-table_th, 
    .w-table_td {
      border-top: none;
    } 
  }
}

.w-text-table {
  @extend .w-table;
  font-size: 14px;
  line-height: 20px;
  @include size(margin, "size_l 0 size_m");
}

.w-text-table_tr {
  @extend .w-table_tr;
}

.w-text-table_th {
  @extend .w-table_th;
  vertical-align: top;
  @include size(padding-left, "size_m");
  @include size(padding-right, "size_m");
}

.w-text-table_td {
  @extend .w-table_td;
  vertical-align: top;
  @include size(padding-left, "size_m");
  @include size(padding-right, "size_m");
}