@import "common/typography";
@import "common/flex";

@font-face {
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  src: local('PT Sans'), local('PTSans-Regular'), url(data:application/x-font-woff;base64,) format('woff2'), url(https://fonts.gstatic.com/s/ptsans/v8/oysROHFTu1eTZ74Hcf8V-VKPGs1ZzpMvnHX-7fPOuAc.woff2) format('woff2');
  unicode-range: U+0100-024F, U+1E00-1EFF, U+20A0-20AB, U+20AD-20CF, U+2C60-2C7F, U+A720-A7FF;
}

.w-img {
  display: block;
  max-width: 100%;
}

.__w-last {
  margin-bottom: 0 !important;
}

.__w-first {
  margin-top: 0 !important;
}

.__w-only {
  @extend .__w-last;
  @extend .__w-first;
}

.__w-to-left {
  float: left;
}

.__w-to-right {
  float: right;
}

.__w-clearfix {

  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

*[lang=ru] .__w-capitalized:first-letter { text-transform: uppercase; }
*[lang=en] .__w-capitalized { text-transform: capitalize; }

/* Formatted number and price */
.w-number,
.w-price {
  word-spacing: -0.1em;
  white-space: nowrap;
}

/* Rouble Sign */
.w-rub,
.w-price__rub { 
  font-family: 'Helvetica Neue', 'Segoe UI', Helvetica, Arial, sans-serif, 'PT Sans';
}

