.w-select {
  position: relative;
  display: inline-block;
}

/* Button */
.w-select_button {
	@extend .font_m;
  
  display: inline-block;
  position: relative;
  white-space: nowrap;
  cursor: pointer;
  line-height: 34px;
  border-radius: 3px;
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);
  padding: 0 size_static("s", "tablet") + 20px 0 size_static("s", "tablet");
  min-width: 60px;
  height: 34px;
  background-origin: content-box;
  background-color: #F7F7F7;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-appearance: none;
  -moz-appearance: none;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &:before {
    content: '';
    display: block;
    position: absolute;
    right: 11px;
    top: 14px;
    @include icon($-icon-disclosure);
  }

  &:hover {
    @extend .w-select_button__hover;
  }

  &:focus {
    @extend .w-select_button__focus;
  }

  &:active {
    @extend .w-select_button__active;
  }
}

.w-select_button__hover {

  &:before {
    @include icon($-icon-disclosure-hover);
  }
}

.w-select_button__focus {
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #187FFC;

  &:before {
    @include icon($-icon-disclosure-hover);
  }
}

.w-select_button__active {
  background: white;

  &:before {
    @include icon($-icon-enclosure-hover);
  }
}

/* Opened */
.w-select__opened {
  
  .w-select_button {
    background: white;
    box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);

    &:before {
      @include icon($-icon-enclosure);
    }

    &:hover {
      @extend .w-select_button__opened-hover;
    }

    &:focus {
      @extend .w-select_button__opened-focus;
    }

    &:active {
      @extend .w-select_button__opened-active;
    }
  }
}

.w-select_button__opened-hover {

  &:before {
    @include icon($-icon-enclosure-hover);
  }
}

.w-select_button__opened-focus {
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #187FFC;

  &:before {
    @include icon($-icon-enclosure-hover);
  }
}

.w-select_button__opened-active {
  background-color: #F7F7F7;

  &:before {
    @include icon($-icon-disclosure-hover);
  }
}

/* List */
.w-select_list {
  @extend .w-dropdown_list;
  left: 0;
  @include size(top, "calc(100% + size_s)");
}

.w-select_list-item {
  @extend .w-dropdown_item;
}

.w-select_link {
  @extend .w-dropdown_link;
}

.w-select__opened {
  
  .w-select_list {
    display: block;
    @include size(top, "calc(100% + size_s)");
  }
}

.w-select__left {
  
  .w-select_list {
    left: 0;
    right: auto;
  }
}

.w-select__right {
  
  .w-select_list {
    left: auto;
    right: 0;
  }
}

.w-select__top {
  
  .w-select_list {
    top: auto;
    @include size(bottom, "calc(100% + size_s)");
  }
}

.w-select__bottom {
  
  .w-select_list {
    @include size(top, "calc(100% + size_s)");
    bottom: auto;
  }
}

/* Wide */
.w-select__wide {
  width: 100%;

  .w-select_button {
    width: 100%;
  }

  .w-select_list {
    width: 100%;
  }
}