.w-form {

  .w-form-item,
  .w-form-action,
  .w-form-actions,
  .w-form-inline,
  .w-body-item {
    @include size(margin-top, "size_m");
    @include size(margin-bottom, "size_s");

    &:first-child {
      @extend .__w-first;
    }

    &:last-child {
      @extend .__w-last;
    }
  }
}

.w-form-item_error {
  @include size(margin-top, "size_xs");
  @extend .font_s;
  color: #B31E1E;
}

.w-label {
  @extend .font_s;
  display: block;
  @include size(margin-bottom, "size_s");
  font-weight: 700;
  text-transform: uppercase;
  color: #666;
}

.w-form-item {

  .w-hint {
    @include size(margin, "size_s 0 0");
  }
}

.w-form-item__error {

  .w-form-item_input,
  .w-form-item_textarea,
  .w-form-item_checkbox-label,
  .w-form-item_radio-label {
    border-color: #CC0E0E;
  }

  .w-form-item_addon-text {
    border-color: #CC0E0E;
  }

  .w-form-item_select {
    box-shadow: inset 0 0 0 1px #CC0E0E;
  }
}

.w-form-item_addon {
  position: relative;
  float: left; 

  & > .w-form-item_select {
    position: relative;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    &:focus {
      z-index: 2;
    }
  }

  & + .w-form-item_control-container {
    position: relative;
    overflow: hidden;
    left: -1px;

    .w-form-item_input {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
}

.w-form-item_addon-text {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  @include size(padding, "0 size_m");
  height: 34px;
  line-height: 33px;
  background-color: #F7F7F7;
}

.w-form-item_input {
  @extend .font_m;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 size_static("s", "tablet");
  width: 100%;
  height: 34px;
  background-color: #fff;
  -webkit-appearance: none;

  &:focus,
  &.__w-state-focus {
    border-color: #187FFC;
  }

  &[disabled],
  &.__w-state-disabled {
    border-color: #DDDDDD;
    background-color: #F7F7F7;
    opacity: 0.7;
  }
}

.w-form-item_select {
  @extend .font_m;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border-radius: 3px;
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);
  padding: 0 size_static("s", "tablet") + 20px 0 size_static("s", "tablet");
  min-width: 60px;
  width: 100%;
  height: 34px;
  @include background("#{$iconsPath}/Icon_Disclosure_1x.png", 9px 6px, calc(100% + 17px) center);
  background-origin: content-box;
  background-color: #F7F7F7;
  -webkit-appearance: none;
  -moz-appearance: none;

  &:hover {
    @include background("#{$iconsPath}/Icon_Disclosure_Hover_1x.png", 9px 6px, calc(100% + 17px) center);
  }

  &:active {
    background: white;
    box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);
  }

  &:focus {
    box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #187FFC;
    @include background("#{$iconsPath}/Icon_Disclosure_Hover_1x.png", 9px 6px, calc(100% + 17px) center);
  }
}

select::-ms-expand {
  display: none;
}

select:focus::-ms-value {
  background-color: #F7F7F7;
  color: black;
}

.w-form-item_textarea {
  display: block;
  @extend .font_m;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: size_static("s", "tablet") size_static("s", "tablet");
  width: 100%;
  min-height: 100px;
  resize: none;
  background-color: #fff;
  -webkit-appearance: none;

  &:focus,
  &.__w-state-focus {
    border-color: #187FFC;
  }

  &[disabled],
  &.__w-state-disabled {
    border-color: #DDDDDD;
    background-color: #F7F7F7;
    opacity: 0.4;
  }
}

.w-form-item_radio__shift {
  padding-left: 21px;
}

.w-form-item_checkbox {
  @include size(margin, "size_s 0 size_s");

  &:first-child {
    @extend .__w-first;
  }

  &:last-child {
    @extend .__w-last;
  }
}

.w-form-item_checkbox-input {
  display: block;
  margin: 0;
  border: none;
  padding: 0;
  width: 0;
  height: 0;
  opacity: 0;

  &:checked {

    & + .w-form-item_checkbox-label::before {
      @include background("#{$iconsPath}/checkmark_1x.png", 12px 10px);
    }

    &[disabled] + .w-form-item_checkbox-label::before,
    &.__w-state-disabled + .w-form-item_checkbox-label::before {
      opacity: 0.4;
    }
  }

  &:focus {
    & + .w-form-item_checkbox-label::before {
      box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #2382F8;
    }
  }

  &[disabled],
  &.__w-state-disabled {

    & + .w-form-item_checkbox-label {
      color: #B2B2B2;

      &::before {
        background: #F7F7F7;
        box-shadow: inset 0px 0px 0px 1px rgba(0,0,0,0.20);
      }
    }
  }
}

.w-form-item_checkbox-label {
  @extend .w-form-item_radio__shift;
  position: relative;
  line-height: 16px;
  display: block;

  &::before {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.20);
    border-radius: 4px;
    width: 16px;
    height: 16px;
    content: '';
    background-color: #fff;
  }

  &:active {

    &::before {
      box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #2382F8;
    }
  }
}

.w-form-item_radio {
  @include size(margin, "size_s 0 size_s");

  &:first-child {
    @extend .__w-first;
  }

  &:last-child {
    @extend .__w-last;
  }
}

.w-form-item_radio-input {
  display: block;
  margin: 0;
  border: none;
  padding: 0;
  width: 0;
  height: 0;
  opacity: 0;

  &:checked {
    
    & + .w-form-item_radio-label::before {
      @include background("#{$iconsPath}/radio_1x.png", 8px 8px);
    }

    &:disabled + .w-form-item_radio-label::before,
    &.__w-state-disabled + .w-form-item_radio-label::before {
      opacity: 0.4;
    }
  }

  &:focus {
    & + .w-form-item_radio-label::before {
      box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #2382F8;
    }
  }

  &[disabled],
  &.__w-state-disabled {

    & + .w-form-item_radio-label {
      color: #B2B2B2;

      &::before {
        background: #F7F7F7;
        box-shadow: inset 0px 0px 0px 1px rgba(0,0,0,0.20);
      }
    }
  }
}

.w-form-item_radio-label {
  @extend .w-form-item_radio__shift;
  line-height: 16px;
  position: relative;
  display: block;

  &::before {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.20);
    border-radius: 50%;
    width: 16px;
    height: 16px;
    content: '';
    background-color: #fff;
  }

  &:active {

    &::before {
      box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #2382F8;
    }
  }
}

.w-form-item_radio-hint {
  @extend .w-hint;
  @extend .w-form-item_radio__shift;
  @include size(margin, "size_xs 0 size_m");
}

::-webkit-input-placeholder {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
}
:-moz-placeholder {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
}
::-moz-placeholder {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
}
:-ms-input-placeholder {  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
}

.w-form-segmented {
  @include flex-box(row, flex-start, flex-start);
}

.w-form-segmented_input {
  position: absolute;
  margin-left: -99999px;
  visibility: hidden;

  &:checked {

    & + .w-form-segmented_label {
      background: #FFFFFF;
      box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);
    }
  }
}

.w-form-segmented_label {
  @include size(height, 'size_xl');
  @include size(line-height, 'size_xl');
  padding: 0 10px;
  color: #000;
  background: #F7F7F7;
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);
  white-space: nowrap;
  cursor: pointer;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &:first-of-type {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }

  &:last-of-type {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
  }
}

@media (max-width: $screen-xs-max) {

  .w-form-actions {
    @include flex-box(column, flex-start, flex-start);

    & > * {

      &:not(:last-child) {
        margin-bottom: size_static("m", "mobile");
      }
    }
  }

  .w-form-item_input {
    height: 40px;
  }

  .w-form-item_select {
    height: 40px;
  }

  .w-form-item_addon-text {
    height: 40px;
    line-height: 38px;
  }

  .w-form-segmented_label {
    height: 40px;
    line-height: 40px;  
  }
}

@media (min-width: $screen-sm) {

  .w-form-inline {
    @include flex-box(row, flex-start, flex-start);

    .w-form-item {
      width: 100%;
      margin: 0;

      &:not(:last-child) {
        margin-right: size_static("s", "tablet");
      }
    }

    .w-form-item_radio,
    .w-form-item_checkbox {
      margin: 0;

      &:not(:last-child) {
        margin-right: size_static("s", "tablet");
      }
    }

    .w-form-action {
      flex-shrink: 0;
      margin: 0;
    }
  }

  .w-form-actions {
    @include flex-box(row, baseline, flex-start);

    & > * {

      &:not(:last-child) {
        margin-right: size_static("s", "tablet");
      }
    }
  }
}

@media (min-width: $screen-md) {

  .w-form-inline {

    .w-form-item {

      &:not(:last-child) {
        margin-right: size_static("s", "desktop");
      }
    }

    .w-form-item_radio,
    .w-form-item_checkbox {
      margin: 0;

      &:not(:last-child) {
        margin-right: size_static("s", "desktop");
      }
    }
  }

  .w-form-actions {

    & > * {

      &:not(:last-child) {
        margin-right: size_static("s", "desktop");
      }
    }
  }
}