.w-form-item_control {
  
  &[type="text"],
  &[type="password"],
  &[type="email"],
  &[type="number"] {
    @extend .font_m;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding: 0 size_static("s", "tablet");
    width: 100%;
    height: 34px;
    -webkit-appearance: none;
    &:focus,
    &.__w-state-focus {
      border-color: #187FFC;
    }

    &[disabled],
    &.__w-state-disabled {
      border-color: #DDDDDD;
      background-color: #F7F7F7;
      opacity: 0.7;
    }
  }

  &[type="checkbox"] {
    display: block;
    margin: 0;
    border: none;
    padding: 0;
    width: 0;
    height: 0;
    opacity: 0;

    &:checked {
      
      & + .w-form-item_checkbox-label::after {
        opacity: 1;
      }
    }

    &:focus {
      & + .w-form-item_checkbox-label::before {
        box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #2382F8;
      }
    }

    &[disabled],
    &.__w-state-disabled {

      & + .w-form-item_checkbox-label {
        color: #B2B2B2;

        &::before {
          background: #F7F7F7;
          box-shadow: inset 0px 0px 0px 1px rgba(0,0,0,0.20);
        }

        &::after {
          opacity: 0.4;
        }
      }
    }
  }

  &[type="radio"] {
    display: block;
    margin: 0;
    border: none;
    padding: 0;
    width: 0;
    height: 0;
    opacity: 0;

    &:checked {
      
      & + .w-form-item_radio-label::after {
        opacity: 1;
      }
    }

    &:focus {
      & + .w-form-item_radio-label::before {
        box-shadow: inset 0px 1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #2382F8;
      }
    }

    &[disabled],
    &.__w-state-disabled {

      & + .w-form-item_radio-label {
        color: #B2B2B2;

        &::before {
          background: #F7F7F7;
          box-shadow: inset 0px 0px 0px 1px rgba(0,0,0,0.20);
        }

        &::after {
          opacity: 0.4;
        }
      }
    }
  }
}

textarea.w-form-item_control {
  @extend .font_m;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: size_static("s", "tablet") size_static("s", "tablet");
  width: 100%;
  min-height: 100px;
  resize: none;
  -webkit-appearance: none;

  &:focus,
  &.__w-state-focus {
    border-color: #187FFC;
  }

  &[disabled],
  &.__w-state-disabled {
    border-color: #DDDDDD;
    background-color: #F7F7F7;
    opacity: 0.4;
  }
}

select.w-form-item_control {
  @extend .font_m;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border-radius: 3px;
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.10);
  padding: 0 size_static("s", "tablet") + 20px 0 size_static("s", "tablet");
  min-width: 60px;
  width: 100%;
  height: 34px;
  @include background("#{$iconsPath}/Icon_Disclosure_1x.png", 9px 6px, calc(100% + 17px) center);
  background-origin: content-box;
  background-color: #F7F7F7;
  -webkit-appearance: none;
  -moz-appearance: none;

  &:hover {
    @include background("#{$iconsPath}/Icon_Disclosure_Hover_1x.png", 9px 6px, calc(100% + 17px) center);
  }

  &:focus {
    box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px #187FFC;
  }
}