.w-accordion {

}

.w-accordion_link {
  cursor: pointer;

  &:hover {

    .w-link__pseudo {
      @extend .w-link__pseudo.__w-state-hover !optional;
    }    
  }

  &:active {

    .w-link__pseudo {
      @extend .w-link__pseudo.__w-state-active !optional;
    }
  }
}

.w-accordion_item {
  overflow: hidden;

  &:first-child {
    @extend .__w-first;
  }

  &:last-child {
    @extend .__w-last;
  }

  &.__w-state-opened {

    .w-accordion_link .w-link__pseudo {
      @extend .w-link__pseudo.__w-state-selected !optional;
      @extend .w-link__pseudo.__w-state-clickable !optional;
    }

    .w-accordion_content {
      display: block;
    }
  }
}

.w-accordion_content {
  display: none;
}

.w-accordion__base {

  .w-accordion_link {
    display: inline-block;
  }

  .w-accordion_item {

    &.__w-state-opened {

      .w-accordion_content {
        @include size(padding-top, "size_m");
      }
    }
  }
}

.w-accordion__panel {

  .w-accordion_item {
    @include size(margin, "size_m 0 size_s");
    border: 1px solid rgba(0,0,0,0.10);
    border-radius: 3px;

    &.__w-state-opened {

      .w-accordion_link {
        border-bottom: 1px solid rgba(0,0,0,0.10);
      }
    }
  }

  .w-accordion_link {
    padding: size_static("s", "desktop") size_static("s", "desktop");
    background: #F7F7F7;
  }

  .w-accordion_content {
    @include size(padding, "size_m");
  }
}