.font_s {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.2px;
}

.font_m {
  font-size: 14px;
  line-height: 20px;
}

.font_l {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
}

.font_xl {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.3px;
}

.font_xxl {
  font-size: 42px;
  line-height: 50px;
  letter-spacing: 1px;
}

@media (max-width: $screen-xs-max) {

  .font_s {
    font-size: 11px;
    line-height: 14px;
  }

  .font_m {
    font-size: 13px;
    line-height: 18px;
  }

  .font_l {
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.2px;
  }

  .font_xl {
    font-size: 19px;
    line-height: 26px;
    letter-spacing: 0.3px;
  }

  .font_xxl {
    font-size: 26px;
    line-height: 40px;
    letter-spacing: 0.5px;
  }  
}

@media (min-width: $screen-sm) and (max-width: $screen-sm-max) {

  .font_s {
    font-size: 11px;
    line-height: 15px;
  }

  .font_m {
    font-size: 13px;
    line-height: 18px;
    letter-spacing: 0.2px;
  }

  .font_l {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
  }

  .font_xl {
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.3px;
  }

  .font_xxl {
    font-size: 34px;
    line-height: 33px;
    letter-spacing: 0.8px;
  }
}

body {
  @extend .font_m;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  color: #000;
}

@font-face {
  font-family: 'als_rublregular';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}

.w-rouble {
  font-family: 'als_rublregular';
  font-size: 1.07em;
}

.w-body-item {
  @include size(margin, "size_m 0 size_s");
}

.w-body-text {
  font-weight: 400;
}

.w-paragraph {
  @extend .w-body-text;
  @extend .w-body-item;
}

.w-body-text-s {
  @extend .font_s;
  color: #666;
  font-weight: 400;
}

.w-hint {
  @extend .w-body-text-s;
  @include size(margin, "size_s 0 size_xs");
}

.w-hint__error {
  color: #CC0E0E;
}

//h1
.w-page-title {
  color: #000;
  display: block;
  @extend .font_xxl;
  @include size(margin, "size_xxl 0 size_xl");
  font-weight: 700;
}

//h2
.w-title {
  color: #000;
  display: block;
  @extend .font_xl;
  @include size(margin, "size_xl 0 size_l");
  font-weight: 700;
}

//h4
.w-subtitle {
  color: #000;
  display: block;
  @extend .font_l;
  @include size(margin, "size_l 0 size_m");
  font-weight: 700;
}