.w-app-nav {
  display: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.03);
  height: map-deep-get($sizes, "xxl", "desktop");
  background-color: rgba(0,0,0,0.03);
  font-size: 0;
  letter-spacing: 0;
  line-height: 0;
}

.w-app-nav_table {
  display: table;
  width: 100%;
}

.w-app-nav_cell-items {
  display: table-cell;
  width: 99%;
}

.w-app-nav_cell-title {
  vertical-align: top;
  display: table-cell;
  width: 1%;
  white-space: nowrap;

  .w-app-nav_item {
    margin-right: -8px;
  }

  .w-app-nav_name {
    color: #000;
  }

  .w-app-nav_item > *,
  .w-link {
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 350px;
  }
}

.w-app-nav_container {
  margin-left: -8px;
  position: relative;
  white-space: nowrap;
  height: 55px;
}

.w-app-nav_group {
  position: absolute;
  width: 100%;
}

.w-app-nav_item {
  @extend .font_m;
  display: inline-block;
  vertical-align: top;
}

.w-app-nav_name__selected {
  background: rgba(0, 0, 0, 0.1);
}

.w-app-nav_title {
  @extend .font_m;
  display: inline-block;
  vertical-align: top;
  @include size(margin-right, "size_l");
  line-height: map-deep-get($sizes, "xxl", "desktop");
  font-weight: bold;
}

.w-app-nav_link {
  display: block;
  @include size(padding, "0 size_s");
  line-height: map-deep-get($sizes, "xxl", "desktop");

  &:hover {
    background-color: rgba(0,0,0,0.05);
  }

  &.__w-state-selected {
    @extend .w-link.__w-state-selected;
    background-color: rgba(0,0,0,0.1);  
  }
}

.w-sidebar {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  transform: translate3d(-10%, 0, 0);
  width: calc(100% - 45px);
  opacity: 0.3;
  transition: all 0.3s ease;
}

.w-sidebar_top {
  color: #fff;
  background-color: #404040;

  .w-sidebar_item {
    @include size(padding, 'size_l');

    &:not(:last-child) {
      border-bottom: 1px solid rgba(255,255,255,0.05);
    }
  }

  .w-sidebar_link {
    @extend .w-link__inverse;
  }

  .w-sidebar_link__pseudo {
    @extend .w-link__inverse-pseudo;  
  }
}

.w-sidebar_bottom {

  .w-sidebar_item {
    background-color: transparent;
    position: relative;
    @for $i from 0 through 20 {
      &:nth-of-type(#{$i}) {
        top: -#{$i}px;
      }
    }

    &:first-child {

      .w-sidebar_link {
        border-top-color: #404040;
      }
    }
  }

  .w-sidebar_link {
    display: block;
    position: relative;
    z-index: 1;
    @include size(padding, 'size_l');
    border-bottom: 1px solid rgba(0,0,0,0.05);
    border-top: 1px solid rgba(0,0,0,0.05);

    &.__w-state-selected {
      z-index: 2;
      background-color: rgba(0,0,0,0.1)
    }
  }
}

.w-sidebar_item {

}

.w-sidebar_item__main {
  font-weight: 700;
}

.w-tabs {
  position: relative;
  overflow: hidden;
  
  &::before {
    display: block;
    position: relative;
    z-index: 1;
    content: 'H';
    @include size(padding, 'calc(size_xs + 1px) size_s');
    border: 1px solid transparent;
    border-bottom-color: rgba(0,0,0,0.1);
    color: transparent;
  }
}

.w-tabs_wrapper {
  z-index: 2;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 50px;
}

.w-tabs_container {
  @include flex-box(row, flex-end, flex-start);
}

.w-tabs_link {
  position: relative;
  margin: 0;
  @include size(padding, 'calc(size_xs + 1px) calc(size_s + 1px) size_xs');
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  border-bottom: 1px solid transparent; 
  white-space: nowrap;

  &.__w-state-selected {
    border: 1px solid rgba(0,0,0,0.1);
    @include size(padding, 'size_xs size_s calc(size_xs + 1px)');
    border-bottom: none;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #fff;
    }
  }
}

@media (max-width: $screen-xs-max) {

  .w-app-nav {
    height: 45px;
  }

  .w-app-nav_title {
    line-height: 45px;
  }

  .w-app-nav_link {
    line-height: 45px;
  }
}

@media (min-width: $screen-sm) {
  .w-app-nav {
    display: block;
  }
}

@media (min-width: $screen-md) {
  
}