.w-ul {
  display: list-item;
  padding-left: 1em;
}

.w-ul_li {
  @extend .w-body-item;
  position: relative;
  list-style: disc;

  &:first-of-type {
    @extend .__w-first;
  }

  &:last-of-type {
    @extend .__w-last;
  }

  .w-ul {
    @extend .w-body-item;
    @include size(padding-left, 'calc(1em + size_m)');

    &:last-of-type {
      @extend .__w-last;
    }

    .w-ul_li {
      list-style: circle;
    }
  }

  .w-ol {
    @extend .w-body-item;
    padding-left: 1em;
  }
}

.w-ol {
  display: list-item;
  counter-reset: item;
}

.w-ol_li {
  @extend .w-body-item;

  &::before {
    float: left;
    margin-right: 8px;
    content: counters(item, ".") ". "; 
    counter-increment: item;
  }

  .w-ul {
    @extend .w-body-item;
    @include size(padding-left, 'calc(1em + size_m)');

    &:last-of-type {
      @extend .__w-last;
    }
  }
}

@media (max-width: $screen-xs-max) {

}

@media (min-width: $screen-sm) and (max-width: $screen-sm-max) {

  .w-ul_li {

    &::after {
      top: 6px;
    }
  }
}

@media (min-width: $screen-md) {

  .w-ul_li {

    &::after {
      top: 8px;
    }
  }
}