@mixin w-badge($modifierName, $color, $textColor: #fff) {
  .w-badge__#{$modifierName} {
    background-color: $color;
    color: $textColor;
  }

  .w-badge__#{$modifierName}-bordered {
    box-shadow: inset 0px 0px 0px 1px rgba(red($color), green($color), blue($color), opacity($color)*0.6);
    color: $color;
  }
}

%w-badge-common {
  display: inline-block;
  border-radius: 3px;
  padding: 2px size_static("xs", "desktop");
  font-weight: 400;
  color: #fff;
  white-space: nowrap;
}

.w-badge {
  @extend %w-badge-common;

  &:not(.w-badge__s) {
    @extend .font_m;
  }

  &:first-letter {
    text-transform: uppercase;
  }
}

.w-badge__s {
  @extend .font_s;
}

@include w-badge("default", #666);
@include w-badge("success", #0B992E);
@include w-badge("danger", #CC0E0E);

.w-badge__multiline {
  white-space: normal;
}