%w-popover {
  border: 1px solid rgba(0,0,0,0.10);
  box-shadow: 0px 4px 10px 0px rgba(0,0,0,0.10);
  border-radius: 2px;
  z-index: get-index(popover);
}

.w-dropdown {
  position: relative;

  &.__w-state-opened {

    .w-dropdown_list {
      display: block;
    }    
  }
}

.w-dropdown__position-underward {

  .w-dropdown_list {
    @include size(top, "calc(100% + size_s)");
  }
}

.w-dropdown__position-aboveward {

  .w-dropdown_list {
    @include size(bottom, "calc(100% + size_s)");
  }
}

.w-dropdown__position-leftward {
  
  .w-dropdown_list {
    right: 0;
  } 
}

.w-dropdown__position-rightward {
  
  .w-dropdown_list {
    left: 0;
  } 
}

.w-dropdown_title {
  cursor: pointer;
}

.w-dropdown_list {
  @extend %w-popover;
  display: none;
  position: absolute;
  min-width: 120px;
  background: #F7F7F7;
}

.w-dropdown_item {
  display: block;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(0,0,0,0.10);
  }
}

.w-dropdown_link {
  display: block;
  @include size(padding, "size_s");
  color: #000;
  cursor: pointer;

  &:hover,
  &.__w-state-hover {
    background: rgba(0,0,0,0.05);
    color: #000;
  }

  &:active,
  &.__w-state-active,
  &.__w-state-selected {
    background: rgba(0,0,0,0.1);
    color: #000;
  }

  &.__w-state-selected {
    cursor: default;
    color: #000;
  }
}

/* Info and Reference */
%info-text {
  @extend .w-body-text-s;
  @include size(padding, "size_s");
  display: none;
  position: absolute;
  z-index: 999;
  max-width: 250px;
  min-width: 150px;
  top: 100%;
  margin-top: 5px;
  background-color: #f7f7f7;
  border: 1px solid rgb(230, 230, 230);
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

%info-text__left {
  left: 0;
  right: auto;
}

%info-text__right {
  left: auto;
  right: 0;
}

%info-icon {
  display: inline-block;
  position: relative;
  top: 3px;
}

/* Reference */
.w-reference {
  @extend %info-icon;
  @include icon($icon_help_normal);

  &:hover {
    @include icon($icon_help_hover);

    .w-reference_text {
      display: block;
    }
  }
}

.w-reference_text {
  @extend %info-text;
}

.w-reference__left .w-reference_text {
  @extend %info-text__left;
}

.w-reference__right .w-reference_text {
  @extend %info-text__right;
}

/* Info */
.w-info {
  @extend %info-icon;
  @include icon($icon-info-normal);

  &:hover {
    @include icon($icon-info-hover);

    .w-info_text {
      display: block;
    }
  }
}

.w-info_text {
  @extend %info-text;
}

.w-info__left .w-info_text {
  @extend %info-text__left;
}

.w-info__right .w-info_text {
  @extend %info-text__right;
}

/* Info Inverse */
.w-info__inverse {
  @extend .w-info;
}