.w-header {
  background-color: #404040;
  position: relative;
  z-index: 12;

  .container {
    position: relative;
    z-index: 3;
    height: 45px;
  }
}

.w-header_logo {
  @include icon($logo_wheely_phone);
  display: block;
  position: relative;
  top: 7px;
  margin: auto;
  border: none;
}

.w-header_project {
  display: none;
  float: left;
  color: rgba(255, 255, 255, 0.4);
  font-size: 20px;
  height: 100%;
  padding-top: 2px;
  @include size(margin-left, "calc(-1 * size_l + size_xs)");
  @include size(margin-right, "size_l");
}

.w-header_nav {
  @extend .__w-clearfix;
  display: none;
  float: right;
  align-items: center;
  justify-content: flex-end;
  height: 100%;

  & > .w-header_nav-item {
    @extend .font_m;
    position: relative;
    align-self: center;
    color: #fff;
    @include size(margin-left, "size_l");
  }
}

.w-header_mobilenav-toddler {
  position: absolute;
  left: 0;
  width: 46px;
  height: 45px;
  transition: all 0.2s ease;

  span {
    display: block;
    position: absolute;
    top: 14px;
    left: 13px;
    margin: 0;
    width: 20px;
    height: 1px;
    background-color: #fff;
    transform-origin: 10px;

    &:nth-of-type(1) {
      transform: translate3d(0,0,0);
      transition: all 0.2s ease;
    }

    &:nth-of-type(2) {
      transform: translate3d(0,7px,0) scaleX(1);
      transition: all 0.2s ease;
      opacity: 1;
    }

    &:nth-of-type(3) {
      transform: translate3d(0,14px,0);
      transition: all 0.2s ease;
    }
  }

  &.__w-state-join {

    span:nth-of-type(1) {
      transform: translate3d(0,7px,0)
    }

    span:nth-of-type(2) {
      transform: translate3d(0,7px,0) scaleX(0);
      opacity: 0;
    }

    span:nth-of-type(3) {
      transform: translate3d(0,7px,0)
    }
  }

  &.__w-state-rotate {
    span:nth-of-type(1) {
      transform: translate3d(0,7px,0) rotate(135deg)
    }

    span:nth-of-type(3) {
      transform: translate3d(0,7px,0) rotate(45deg)  
    }
  }
}

@media (min-width: $screen-sm) {
  .w-header {

    .container {
      height: size_static("xxl", "desktop");
      line-height: size_static("xxl", "desktop");
    }
  }

  .w-header_logo {
    @include icon($logo_wheely_tablet);
    top: 10px;
    float: left;
    margin-right: size_static("l", "tablet");
  }

  .w-header_mobilenav-toddler {
    display: none;
  }

  .w-header_nav {
    display: flex;
    height: 100%;
  }

  .w-header_project {
    display: block;
  }
}

@media (min-width: $screen-md) {

  .w-header_logo {
    margin-right: size_static("l", "desktop");
  }
}

/* Language */
.w-flag {
  display: inline-block;
  vertical-align: middle;

  &[data-country-code="ru"] {
    @include icon($lang_ru);
  }

  &[data-country-code="en"] {
    @include icon($lang_uk);
  }
}

.w-header {

  .w-flag {
    @include size(margin-right, "size_xs");
  }
}

