  .w-header_nav-left {
    float: left;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 100%;

    .w-header_nav-item {
      @include size(margin-right, "size_l");
    }
  }

  .w-header_nav-right {
    float: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;

    .w-header_nav-item {
      @include size(margin-left, "size_l");
    }
  }

  .w-header_nav-left,
  .w-header_nav-right {
    .w-header_nav-item {
      @extend .font_m;
      position: relative;
      align-self: center;
      color: #fff;
    }
  }
}
