.w-footer {
  border-top: 1px solid #000;
  @include size(margin-top, 'size_xl');
  @include size(padding-top, 'size_xl');
  @include size(padding-bottom, 'size_xxl');
}

.w-footer_item {
  @include size(margin-bottom, 'size_s');
}

.w-footer_marketing {
  @include size(margin-top, 'size_l');
}

.w-footer_socials {
  @include size(margin-top, 'size_l');  
  @include flex-box(row, center, flex-start);
}

.w-footer_social {

  &:not(:last-child) {
    @include size(margin-right, 'size_l');
  }
}

.w-footer_social__instagram {
  @include icon($social-instagram);

  &:hover {
    @include icon($social-instagram-hover);
  }

  &:active {
    @include icon($social-instagram-pressed);  
  }
}

.w-footer_social__facebook {
  @include icon($social-facebook);  

  &:hover {
    @include icon($social-facebook-hover);
  }

  &:active {
    @include icon($social-facebook-pressed);  
  }
}

.w-footer_social__twitter {
  @include icon($social-twitter);   

  &:hover {
    @include icon($social-twitter-hover);
  }

  &:active {
    @include icon($social-twitter-pressed);  
  }
}

.w-footer_social__vk {
  @include icon($social-vkontakte);   

  &:hover {
    @include icon($social-vkontakte-hover);
  }

  &:active {
    @include icon($social-vkontakte-pressed);  
  }
}

@media (max-width: $screen-xs-max) {

  .w-footer_col {
    @include size(margin-bottom, 'size_l');
  }
}