@mixin flex() {
    width: 100%;
    height: 100%;

    // Display as flex
    @include display-flex();

    // How to align elements along main axis
    @include cross-prop(justify-content, flex-start);

    // How to align elements along transverse axis
    @include cross-prop(align-items, stretch);
}

.w-flex-rows {
    // Define this css-class as flex-container
    @include flex();

    // Direction of the main axis
    @include cross-prop(flex-direction, column);

    // Multilines on (for the rows)
    @include cross-prop(flex-wrap, nowrap);

    // How to align rows by vertical
    @include cross-prop(align-content, stretch);
}

.w-flex-cols {
    // Define this css-class as flex-container
    @include flex();

    // Direction of the main axis
    @include cross-prop(flex-direction, row);

    // Multilines off (no rows)
    @include cross-prop(flex-wrap, nowrap);
}

.w-flex-rows_item {
    @include cross-prop(flex-grow, 1);
    @include cross-prop(flex-shrink, 1);
}

.w-flex-cols_item {
    @include cross-prop(flex-grow, 1);
    @include cross-prop(flex-shrink, 1);
}
