.w-btn {
  @extend .font_m;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  border: none;
  border-radius: 3px;
  box-shadow: inset 0px -1px 0px 0px rgba(0, 0, 0, 0.20);
  min-width: 89px;
  max-width: 100%;
  @include size(padding, "0 size_s");
  height: 34px;
  cursor: pointer;
  line-height: 34px;
  text-align: center;
  color: #fff;
  transition: background-color 0.1s ease;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;

  &[disabled],
  &.__w-state-disabled {
    cursor: default;
    background-color: hsl(0, 0, 90);
    color: #B2B2B2;

    &:hover {
      background-color: hsl(0, 0, 90);
      color: #B2B2B2;      
    }
  }
}

.w-btn {
  background-color: hsb(213, 93, 80);

  &:hover,
  &.__w-state-hover,
  &:focus,
  &.__w-state-focus {
    background-color: hsb(213, 93, 100);
  }

  &:active,
  &.__w-state-active {
    background-color: hsb(213, 93, 40);
  }
}

.w-btn__secondary {
  background-color: hsb(0, 0, 97);
  box-shadow: inset 0px -1px 0px 0px rgba(0,0,0,0.20), inset 0px 0px 0px 1px rgba(0,0,0,0.03);
  color: #000;

  &:hover,
  &.__w-state-hover,
  &:focus,
  &.__w-state-focus {
    background-color: hsb(0, 0, 97);
  }

  &:active,
  &.__w-state-active {
    background-color: hsb(0, 0, 97);
  }
}

.w-btn__danger {
  background-color: hsb(360, 93, 80);

  &:hover,
  &.__w-state-hover,
  &:focus,
  &.__w-state-focus {
    background-color: hsb(360, 93, 100);
  }

  &:active,
  &.__w-state-active {
    background-color: hsb(360, 93, 40);
  }
}

@-moz-document url-prefix() {
  .w-btn {
    line-height: 32px;
  }
}

@media (max-width: $screen-xs-max) {
  .w-btn {
    height: 40px;
    line-height: 40px;
  }
}
