body {
  background-color: rgba(0,0,0,0.03);
}

.w-page {
  position: relative;
}

.w-page_container {
  min-height: 100vh; 
  position: relative;
  z-index: 2;
  transform: translate3d(0, 0, 0);
  background-color: #fff;
  transition: all 0.3s ease;
}

@media (max-width: $screen-xs-max) {

  .w-page__nav-opened {
    overflow-x: hidden;

    .w-page_container {
      transform: translate3d(calc(100% - 45px), 0, 0);
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1), -1px 0 0 0 rgba(0,0,0,0.1);
    }

    .w-sidebar {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  .w-page__modal {

    .w-page_container,
    .w-sidebar {
      display: none;
    }
  }
}