.w-link {
  display: inline;
  cursor: pointer;
  text-decoration: none;
  color: #0E64CC;

  &:hover,
  &.__w-state-hover {
    color: #187FFC;
  }

  &:active,
  &.__w-state-active,
  &.__w-state-selected {
    color: #000;
  }

  &.__w-state-selected {
    cursor: default;
  }

  &.__w-state-clickable {
    color: #0E64CC;
    cursor: pointer;

    &:hover {
      color: #187FFC;
    }

    &:active {
      color: #000;
    }
  }
}

.w-link__pseudo {
  border-bottom: 1px dashed rgba(24, 127, 252, 0.3);

  &:hover,
  &.__w-state-hover {
    color: #187FFC;
    border-bottom: 1px dashed rgba(18, 125, 255, 0.3);
  }

  &:active,
  &.__w-state-active {
    color: #000;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
  }

  &.__w-state-selected {
    cursor: default;
    color: #000;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
  }

  &.__w-state-clickable {
    cursor: pointer;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
    color: #000;

    &:hover,
    &.__w-state-hover {
      border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
      color: #666;
    }

    &:active,
    &.__w-state-active {
      color: #000;
      border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
    }
  }

  &[disabled] {
    opacity: 0.5;
    color: #000;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
  }
}

.w-link__btn {
  display: inline-block;
  line-height: 34px;
}

.w-link__inverse {
  color: #fff;

  &:hover,
  &.__w-state-hover {
    color: #fff;
    opacity: 0.5;
  }

  &:active,
  &.__w-state-active {
    color: #fff;
    opacity: 1;
  }

  &.__w-state-selected {
    cursor: default;
    color: #fff;
    opacity: 0.5;
  }
}

.w-link__inverse-pseudo {
  border-bottom: 1px dashed rgba(255,255,255,0.3);

  &.__w-state-selected {
    opacity: 0.5;
    color: inherit;
  }

  &.__w-state-clickable {
    border-bottom: 1px dashed rgba(255,255,255,0.3);
    color: inherit;
    opacity: 1;

    &:hover,
    &.__w-state-hover {
      border-bottom: 1px dashed rgba(255,255,255,0.3);
      color: inherit;
      opacity: 0.5;
    }

    &:active,
    &.__w-state-active {
      color: inherit;
      border-bottom: 1px dashed rgba(255,255,255,0.3);
      opacity: 1;
    }
  }

  &[disabled] {
    opacity: 0.5;
    color: inherit;
    border-bottom: 1px dashed rgba(255,255,255,0.3);
  }
}

@media (max-width: $screen-xs-max) {

  .w-link__btn {
    line-height: 40px;
  }  
}