@import "bootstrap-sass/assets/stylesheets/bootstrap/mixins";

@mixin size($prop, $value) {
  $mobile: $value;
  $tablet: $value;
  $desktop: $value;
  @each $sizeName, $sizeValue in $sizes {
    $mobile: str-replace($mobile, "size_" + $sizeName, map-deep-get($sizes, $sizeName, "mobile"));
    $tablet: str-replace($tablet, "size_" + $sizeName, map-deep-get($sizes, $sizeName, "tablet"));
    $desktop: str-replace($desktop, "size_" + $sizeName, map-deep-get($sizes, $sizeName, "desktop"));
  }

  #{$prop}: $desktop;

  @media (max-width: $screen-xs-max) {
    #{$prop}: $mobile;
  }

  @media (min-width: $screen-sm) and (max-width: $screen-sm-max) {
    #{$prop}: $tablet;
  }
}

@mixin background($url, $size: 100%, $position: center center, $repeat: no-repeat) {
  background-image: url("#{$url}");
  background-size: $size;
  background-position: $position;
  background-repeat: $repeat;

  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    background-image: url(str-replace("#{$url}", "1x", "2x"));
    background-size: $size;
  }
}

@mixin icon($icon) {
  @include sprite($icon);

  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    $backgroundImage: str-replace(nth($icon, 9), '1x', '2x');
    background-image: url($backgroundImage);
    background-size: nth($icon, 7) nth($icon, 8);
  }
  background-repeat: no-repeat;
}

@mixin flex-box($direction, $align: center, $justify: center, $display: flex) {
  display: $display;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}

.display-flex {
    display: -webkit-box;      // OLD - iOS 6-, Safari 3.1-6
    display: -moz-box;         // OLD - Firefox 19- (buggy but mostly works)
    display: -ms-flexbox;      // TWEENER - IE 10
    display: -webkit-flex;     // NEW - Chrome
    display: flex;             // NEW, Spec - Opera 12.1, Firefox 20+
}

@mixin display-flex() {
    @extend .display-flex;
}

@mixin cross-prop($name, $val) {
    -webkit-#{$name}: $val;
            #{$name}: $val;
}

@mixin flex-centered() {
    @extend .display-flex;
    @include flex-centered-vertical();
    @include flex-centered-horizontal();
}

@mixin flex-centered-vertical() {
    @extend .display-flex;
    @include cross-prop(align-items, center);
}

@mixin flex-centered-horizontal() {
    @extend .display-flex;
    @include cross-prop(justify-content, center);
}
