@mixin w-notification($modifierName, $color) {
  .w-notification__#{$modifierName} {
    background-color: hsb(HSBhue($color), HSBsaturation($color), HSBbrightness($color), 0.1);
    color: hsb(HSBhue($color), HSBsaturation($color), HSBbrightness($color) - 10);
  }
}

.w-notification {
  display: inline-block;
  border-radius: 3px;
  @include size(padding, "size_s");
}

.w-notification_title {
  display: inline;
  font-weight: 700;
}

.w-notification_text {
  display: inline;
}

@include w-notification("success", #0B992E);
@include w-notification("danger", #CC0E0E);
@include w-notification("warning", #B3910C);
@include w-notification("neutral", #929292);