@import "bootstrap-sass/assets/stylesheets/bootstrap/mixins/grid";
@import "bootstrap-sass/assets/stylesheets/bootstrap/mixins/grid-framework";

.w-hr {
  display: block;
  width: 100%;
  height: 1px;
  background-color: rgba(0,0,0,0.1);
  @include size(margin, "size_l auto");
}

.w-section-hr {
  @extend .container-l;
  @include size(margin, "size_xl auto");
  height: 1px;
  display: block;
  width: 100%;

  &::before {
    display: block;
    height: 100%;
    background-color: rgba(0,0,0,0.1);
    content: '';
  }
}

.w-hr__xl {
  @extend .w-hr;
  @include size(margin, "size_xl auto");
}

.row {
  @include make-row;
}

@include make-grid-columns;

@include make-grid(xs);

.container,
.container-l {
  margin-left: auto;
  margin-right: auto;
  padding: 0 size_static("m", "mobile");
}

@media (max-width: $screen-xs-max) {

  .__w-tablet,
  .__w-desktop {

    &:not(.__w-phone) {
      display: none !important;
    }
  }

  .container,
  .container-l {
    width: 100%;
    padding: 0 size_static("m", "tablet");
  }

  [class*="col-md"],
  [class*="col-sm"],
  [class*="col-xs"] {
    padding-left: size_static("m", "mobile");
    padding-right: size_static("m", "mobile");
  }

  .row {
    margin-left: - size_static("m", "mobile");
    margin-right: - size_static("m", "mobile"); 
  }
}

@media (min-width: $screen-sm-min) {
  @include make-grid(sm);

  .container,
  .container-l {
    padding: 0 size_static("l", "tablet");
    width: 100%;
  }
}

@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {

  [class*="col-md"],
  [class*="col-sm"],
  [class*="col-xs"] {
    padding-left: size_static("m", "tablet");
    padding-right: size_static("m", "tablet");
  }

  .__w-phone,
  .__w-desktop {

    &:not(.__w-tablet) {
      display: none !important;
    }
  }

  .row {
    margin-left: - size_static("m", "tablet");
    margin-right: - size_static("m", "tablet"); 
  }
}

@media (min-width: $screen-md-min) {
  @include make-grid(md);

  .container {
    padding: 0 7.5%;
    max-width: 1920px;
  }

  .container-l {
    padding: 0 4%;
  }

  .__w-phone,
  .__w-tablet {

    &:not(.__w-desktop) {
      display: none !important;
    }
  }
}